/****************************************************************************************/
/* Togo Client */
/****************************************************************************************/
@import "_client_variables";

.c9 dl,
.entry-content p,
.entry-footer {
	line-height: $line-height-base;
}

.editor-styles-wrapper .block-editor-rich-text__editable a,
.entry-content p a:not(.wp-block-button__link):not(.nav-tabs):not([class|=c9-share]):not(.c9-smooth),
.entry-title a,
.entry-content a {
	color: $link-color;
	text-decoration: underline;
	text-underline-offset: 2px;
	text-decoration-thickness: 2px;
	-webkit-text-decoration-style: dotted;
	text-decoration-style: dotted;
}

.entry-title a {
	text-decoration: none;
}

.entry-title a:hover {
	text-decoration: underline;
	text-decoration-thickness: 2px;
	text-underline-offset: 2px;
}

.woocommerce-store-notice a,
p.demo_store a {
	text-decoration-thickness: 2px;
	text-underline-offset: 2px;
	-webkit-text-decoration-style: dotted;
	text-decoration-style: dotted;
}

.woocommerce-store-notice a:focus,
p.demo_store a:focus,
.woocommerce-store-notice a:hover,
p.demo_store a:hover {
	-webkit-text-decoration-style: solid;
	text-decoration-style: solid;
}

.entry-content a:hover,
a:hover {
	color: $link-hover-color;
	text-decoration: underline;
	text-decoration-thickness: 2px;
	text-underline-offset: 2px;
}

.site :focus {
	outline-color: $link-color;
}

.site-main {
	-webkit-animation-duration: .38s;
	animation-duration: .38s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-name: slideInUp;
	animation-name: slideInUp;
}

@media only screen and (max-width: 414px) {
	.entry-content {
		max-width: 100vw;
	}
}

/****************************************************************************************/
/* nav header */
/****************************************************************************************/
@media only screen and (min-width: 992px) {
	.header-navbar .site-branding {
		min-width: 225px;
	}
}

.header-navbar .c9-site-title,
.header-navbar .site-description {
	margin-bottom: 5px;
	text-align: left;
	line-height: 1.2;
}

.header-navbar .c9-site-title a {
	text-decoration: none;
}

.header-navbar .site-branding,
.header-navbar .c9-site-title {
	display: block;
	text-align: left;
}

.header-navbar .navbar {
	background-color: transparent;
}

.header-navbar .nav .nav-item .dropdown-item,
.header-navbar .nav .nav-item .nav-link,
.header-navbar .nav-item,
.header-navbar .search {
	line-height: 1;
	height: 44px;
}

.header-navbar .dropdown>.dropdown-toggle {
	margin-top: -2px;
}

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search,
.navbar-light .navbar-nav .nav-link {
	color: $dark;
}

.header-navbar .navbar-toggler:focus,
.header-navbar .nav-search .btn-nav-search:focus {
	color: $dark;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
	background-color: $light;
}

.header-navbar .justify-content-center {
	justify-content: flex-end !important;
}

.dropdown-toggle::after {
	border-right: none;
	border-left: none;
}

.header-navbar .nav .search .nav-link .fa-search,
.header-navbar .nav .nav-link .fa-shopping-cart,
.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search {
	font-size: 14px;
}

.header-navbar .navbar .nav .dropdown-toggle::after {
	content: ' \f0d7';
	font-family: FontAwesome;
	margin: 0px;
	padding: 1px 0px 0px 2px;
	border: none;
	width: auto;
	height: 14px;
	vertical-align: 0;
	font-size: 14px;
}

.header-navbar .nav-item {
	margin: 0px 14px;
}

.header-navbar .nav-item .dropdown-menu .nav-item {
	margin: 0px 6px;
}

.header-navbar .nav-item .nav-highlight {
	content: "";
	position: absolute;
	z-index: -1;
	left: 0;
	right: 100%;
	bottom: 0;
	background: $dark;
	height: 4px;
	transition: right .5s;
}

.header-navbar .nav-item>.nav-link:hover>.nav-highlight,
.header-navbar .nav-item>.nav-link:focus>.nav-highlight,
.header-navbar .nav-item:focus>.nav-link>.nav-highlight {
	right: 0% !important;
}

.header-navbar .navbar .nav .search .nav-search a {
	font-size: 14px;
}

.header-navbar .dropdown-item,
.header-navbar .nav-link {
	color: $dark;
	font-size: 14px;
	font-family: $font-family-sans-serif;
	text-transform: none;
	position: relative;
	letter-spacing: .8px;
	padding: 14px 0px;
}

.header-navbar .navbar .nav .nav-item .dropdown-item {
	padding: 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item .nav-link {
	padding: 14px 8px;
}

.header-navbar .nav .nav-item .dropdown-menu {
	background-color: $primary;
	border-radius: 0px;
	margin-top: 38px;
	border: 0px;
	-webkit-animation: fadeInFromNone 0.5s ease-out;
	animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .navbar-small .nav-item .dropdown-menu {
	margin-top: 5px;
	position: absolute;
}

@media only screen and (max-width: 991px) {
	.header-navbar .nav-item.show {
		height: auto;
	}

	.header-navbar .nav .nav-item .dropdown-menu {
		margin-top: 0px;
	}

	.header-navbar .nav .nav-item .dropdown-menu {
		min-width: 100%;
	}
}

.header-navbar .nav .nav-item .dropdown-menu.show {
	display: block;
	-webkit-animation: fadeInFromNone 0.5s ease-out;
	animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav-order {
	display: inline-block;
	font-size: 2em;
}

.header-navbar .nav-order:empty {
	display: none !important;
}

.header-navbar .navbar-light .navbar-toggler {
	margin-right: 0;
}

.header-navbar .nav-order .nav-link,
.header-navbar .dropdown-item:focus,
.header-navbar .dropdown-item:hover {
	text-decoration: none;
	color: $link-color;
}

@-webkit-keyframes fadeInFromNone {
	0% {
		display: none;
		opacity: 0;
	}

	1% {
		display: block;
		opacity: 0;
	}

	100% {
		display: block;
		opacity: 1;
	}
}

@keyframes fadeInFromNone {
	0% {
		display: none;
		opacity: 0;
	}

	1% {
		display: block;
		opacity: 0;
	}

	100% {
		display: block;
		opacity: 1;
	}
}

@-webkit-keyframes slideInUp {
	from {
		transform: translate3d(0, 5px, 0);
		opacity: 0;
	}

	to {
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

@keyframes slideInUp {
	from {
		transform: translate3d(0, 5px, 0);
		opacity: 0;
	}

	to {
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

.nav-woocommerce:hover a:before,
.nav-woocommerce:hover a .nav-highlight,
.nav-link .view-cart {
	display: none;
}

@media only screen and (max-width: 991px) {

	.nav-search,
	.nav-toggle,
	.nav-order {
		display: inline-block;
		width: 40px;
	}

	.navbar-toggler[aria-expanded=true] .fa-bars:before {
		content: '\f00d';
	}

	.nav-link .view-cart {
		display: inline;
	}

}

/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/
.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
	background-color: $dark;
	color: $light;
}

.header-navbar .nav .nav-item .nav-link {
	transition: color .3s ease-in-out;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-order-now.active>.nav-link,
.c9 .header-navbar .navbar-nav .nav-item.c9-order-now>.nav-link,
.is-style-c9-btn-green .wp-block-button__link {
	padding-left: 14px;
	padding-right: 14px;
	color: $light;
	background: linear-gradient(151deg, rgb(122, 220, 180) 0%, rgb(0, 208, 130) 100%);
	border-radius: 34px;
}

.header-navbar .navbar .nav>.nav-item.active.c9-order-now>.nav-link span,
.header-navbar .navbar .nav>.nav-item.nav-highlight.c9-order-now>.nav-link span,
.header-navbar .navbar .nav>.nav-item.c9-order-now:hover>.nav-link span {
	display: none;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-order-now .nav-link:hover,
.c9 .header-navbar.navbar-small .navbar-nav .nav-item.c9-order-now .nav-link:hover,
.is-style-c9-btn-green:hover .wp-block-button__link {
	background: linear-gradient(343deg, rgb(255, 230, 5) 0%, rgb(242, 255, 3) 100%);
	color: $dark !important;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-yellow-btn>.nav-link {
	padding-left: 14px;
	padding-right: 14px;
	border-radius: 34px;
	background: linear-gradient(343deg, rgb(255, 230, 5) 0%, rgb(242, 255, 3) 100%);
	color: $dark;
}

.header-navbar .navbar .nav>.nav-item.active.c9-yellow-btn>.nav-link span,
.header-navbar .navbar .nav>.nav-item.nav-highlight.c9-yellow-btn>.nav-link span,
.header-navbar .navbar .nav>.nav-item.c9-yellow-btn:hover>.nav-link span {
	display: none;
}

.c9 .header-navbar .navbar-nav .nav-item.c9-yellow-btn .nav-link:hover,
.c9 .header-navbar.navbar-small .navbar-nav .nav-item.c9-yellow-btn .nav-link:hover {
	color: $light;
	background: linear-gradient(151deg, rgb(122, 220, 180) 0%, rgb(0, 208, 130) 100%);
}

.header-navbar .navbar.navbar-small .nav .nav-item.active:not(.c9-order-now)>.nav-link {
	color: $dark;
}

.header-navbar .navbar:not(.navbar-small) .nav-link:hover,
.header-navbar .dropdown-item:hover,
.header-navbar .navbar-toggler:hover,
.header-navbar .nav-link:hover,
.header-navbar .nav>.nav-item>.nav-link:focus,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-woocommerce .nav-link:hover,
.header-navbar .nav-woocommerce .nav-link:focus,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
	color: $primary;
}

.header-navbar .navbar.navbar-small .nav .nav-item.active>.nav-link:hover {
	color: $primary;
}

/****************************************************************************************/
/* mobile navigation background*/
/****************************************************************************************/
@media only screen and (max-width: 667px) {
	.header-navbar .navbar {
		background-color: $light;
	}
}

@media only screen and (max-width: 991px) {

	.header-navbar .navbar-collapse,
	.header-navbar .navbar-collapse.show {
		background-color: $light;
		left: 0;
	}
}

/****************************************************************************************/
/* typography*/
/****************************************************************************************/

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.c9 h1,
.c9 h2,
.c9 h3,
.c9 h4,
.c9 h5,
.c9 h6,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6,
.c9-h,
.c9-h.h,
.c9-txl,
.display-1,
.display-2,
.display-3,
.display-4,
.display-5,
.display-6,
.editor-styles-wrapper .c9-cta .c9-h p {
	letter-spacing: -.8px;
	font-weight: $headings-font-weight;
}

.single-post .c9 h1 {
	text-transform: none;
	font-weight: 700;
	font-size: 2.8em;
}

p.wp-block-subhead,
.subhead-h,
.c9-sh,
.c9-heading .c9-h .text-muted,
.c9-heading .c9-sh .text-muted,
.c9-heading .c9-txl .text-muted {
	font-family: $font-family-sans-serif;
	font-weight: 200;
}

.c9 input[type="text"],
.c9 input[type="email"],
.c9 input[type="url"],
.c9 input[type="password"],
.c9 input[type="tel"],
.c9 textarea,
.c9 #s {
	font-family: $font-family-base;
	border-radius: 0px;
}

.c9 textarea {
	line-height: 1.5;
}

.entry-content p.has-background,
.editor-styles-wrapper p.has-background {
	margin: 0px;
	padding: 2px 4px;
}

.entry-content p.has-background strong,
.editor-styles-wrapper p.has-background strong {
	display: inline-block;
	line-height: 1;
}

.c9-block-post-grid header .c9-block-post-grid-title a {
	font-weight: 700;
}

#fullscreensearch input[type=search] {
	border-radius: 34px;
}

#fullscreensearch .search-close {
	font-size: 16px;
	height: 40px;
	width: 40px;
	line-height: 1;
	border-radius: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 3px solid $dark;
}

#fullscreensearch .search-close:focus {
	border: 3px solid $gray-500;
}

#fullscreensearch .search-close:hover {
	opacity: .8;
}

#fullscreensearch input[type=search]:focus,
#fullscreensearch form .btn:focus,
#fullscreensearch .search-close:focus {
	box-shadow: 1px 1px 8px rgba(0, 0, 0, .3);
	outline: 0;
}

#fullscreensearch input[type=search]:focus {
	border: 3px solid rgb(151, 151, 151);
}

#fullscreensearch form .btn {
	font-family: $font-family-base;
	padding: 16px 55px;
	font-size: 1.4em;
	border-radius: 34px;
	margin-top: 20px;
}

.primary-font-family {
	font-family: $headings-font-family !important;
}

.entry-content .c9-grid-has-background .has-text-color a {
	color: $light !important;
}

.entry-content .c9-grid-has-background .has-text-color a:hover {
	color: $primary !important;
}


.entry-content .has-covertnine-palette-1-color a {
	color: $light;
}

.entry-content .has-covertnine-palette-1-color a:hover {
	color: $gray-500;
}

/* Custom Togo Colors and outline button colors */
.is-style-outline .wp-block-button__link,
.is-style-outline .wp-block-button__link:not([href]):not([tabindex]) {
	border: 2px solid;
}

.is-style-outline,
.is-style-outline .has-text-color {
	background-color: transparent !important;
}

.is-style-outline .wp-block-button__link,
.is-style-outline .wp-block-button__link:not([href]):not([tabindex]) {
	background-color: inherit !important;
}

.is-style-outline .has-color-faded-green-color,
.color-faded-green {
	color: #7adcb4 !important;
}

.has-color-faded-green-background-coolor {
	background-color: #7adcb4 !important;
}

.is-style-outline .has-color-green-color,
.has-color-green-color {
	color: $primary !important;
}

.has-color-green-background-color {
	background-color: $primary !important;
}

.is-style-outline .has-color-yellow-color,
.has-color-yellow-color {
	color: #ffe605 !important;
}

.has-color-yellow-background-color {
	background-color: #ffe605 !important;
}

.is-style-outline .has-color-light-gray-color,
.has-color-light-gray-color {
	color: #b0b0b0 !important;
}

.has-color-light-gray-background-color {
	background-color: #b0b0b0 !important;
}

.is-style-outline .has-color-dark-gray-color,
.has-color-dark-gray-color {
	color: #242424 !important;
}

.has-color-dark-gray-background-color {
	background-color: #242424 !important;
}

.is-style-outline .has-color-white-color,
.has-color-white-color {
	color: $light !important;
}

.has-color-white-background-color {
	background-color: $light !important;
}

.is-style-outline .has-color-orange-color,
.has-color-orange-color {
	color: #ffb442 !important;
}

.has-color-orange-background-color {
	background-color: #ffb442 !important;
}

.is-style-outline .has-color-alt-green-color,
.has-color-alt-green-color {
	color: #00ff8c !important;
}

.has-color-alt-green-background-color {
	background-color: #00ff8c !important;
}

.is-style-outline .has-color-blue-color,
.has-color-blue-color {
	color: #2e46d3 !important;
}

.has-color-blue-background-color {
	background-color: #2e46d3 !important;
}

.is-style-outline .has-color-black-color,
.has-color-black-color {
	color: $dark !important;
}

.has-color-black-background-color {
	background-color: $dark !important;
}

/****************************************************************************************/
/* post navigation, tags, categories, on single posts */
/****************************************************************************************/
.single .navigation .nav-next a,
.single .navigation .nav-previous a {
	font-family: $headings-font-family;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: 1px;
}

.single .navigation .nav-next a:hover,
.single .navigation .nav-previous a:hover {
	color: $primary;
}

@media only screen and (min-width: 768px) {
	.single .entry-footer {
		padding: 0px;
	}
}

.entry-footer .cat-links {
	font-size: 14px;
}

.entry-footer .cat-links,
.entry-footer .tag-links {
	margin-bottom: 20px;
	display: block;
}

/* tags */
.i-tag,
.tags-links,
.tags-links a {
	font-weight: 900;
	letter-spacing: 1px;
	text-decoration: none;
	text-transform: uppercase;
	color: $gray-900;
	font-size: 12px;
}

.tags-links a {
	padding-left: 8px;
}

.i-tag:hover,
.tags-links a:hover {
	color: $gray-500;
}

.i-tag:before,
.tags-links a:before {
	display: inline;
	content: "\f02b";
	font: normal normal normal 1.2em/1 FontAwesome;
	color: $primary;
	padding-right: 2px;
}

/****************************************************************************************/
/* blog index */
/****************************************************************************************/
.container-posts {
	margin-top: 3em;
}

.c9-block-post-grid header .c9-block-post-grid-title {
	margin-bottom: 0px;
}

.c9-block-post-grid .c9-block-post-grid-text {
	display: grid;
	align-items: center;
	justify-content: center;
}

.entry-meta {
	color: #121212;
}

/****************************************************************************************/
/* blog single */
/****************************************************************************************/
.entry-big-header .entry-title-box .entry-meta,
.entry-big-header .entry-title-box .entry-meta .posted-on,
.entry-big-header .entry-title-box .entry-meta .posted-on a,
.entry-big-header .entry-title-box .entry-meta .byline,
.entry-big-header .entry-title-box .entry-meta .author a {
	font-size: 12px;
	color: $light;
}

/* search results */
.updated {
	display: none;
}

.entry-big-header .entry-title-box .entry-meta .posted-on a,
.entry-big-header .entry-title-box .entry-meta .author a {
	text-decoration: underline;
	text-decoration-thickness: 2px;
	text-underline-offset: 2px;
}

.entry-big-header .entry-title-box .entry-meta a:hover {
	color: #4CAAB1 !important;
}

/****************************************************************************************/
/* WordPress Gutenberg Block Specifics */
/****************************************************************************************/

/* invert link colors on posts grid */
.entry-content .light-color-text .c9-block-post-grid-title a,
.editor-styles-wrapper .light-color-text .c9-block-post-grid-title a {
	color: $light;
	font-weight: 700;
	text-decoration: none;
}

/* carousel captions */
.carousel-item .carousel-caption h5 {
	font-weight: 700;
}

/* tabs + toggles */
.editor-styles-wrapper .c9-vertical-tabs .nav-pills .nav-link,
.editor-styles-wrapper .c9-horizontal-tabs .nav-tabs .nav-link,
.c9-vertical-tabs .nav-pills .nav-link,
.c9-horizontal-tabs .nav-tabs .nav-link {
	font-family: $font-family-sans-serif;
	text-transform: capitalize;
	letter-spacing: 0;
	font-weight: $font-weight-bold;
	font-size: 16px;
	border-radius: 34px;
}

.c9-vertical-tabs .nav-pills .nav-link:hover,
.c9-horizontal-tabs .nav-tabs .nav-link:hover {
	background-color: $secondary;
	color: $light;

}

.editor-styles-wrapper .c9-vertical-tabs .nav-pills .nav-link.active,
.c9 .c9-vertical-tabs .nav-pills .nav-link.active,
.c9-horizontal-tabs .nav-tabs .nav-link.active,
.editor-styles-wrapper .c9-horizontal-tabs .nav-tabs .nav-link.active {
	background-color: $primary;
	color: $dark;
}

.editor-styles-wrapper .c9-vertical-tabs .nav-pills .nav-link,
.c9 .c9-vertical-tabs .nav-pills .nav-link {
	margin-bottom: 10px;
}

.c9-toggles .c9-toggles-toggle .c9-toggles-item-heading .c9-toggles-toggle-label,
.c9-toggles.is-style-toggles-small .c9-toggles-toggle .c9-toggles-item-heading .c9-toggles-toggle-label {
	letter-spacing: 0px;
	font-weight: 700;
}

.entry-content .c9-horizontal-tabs>.nav.nav-tabs,
.editor-styles-wrapper .is-root-container .c9-horizontal-tabs>.nav.nav-tabs {
	margin: 30px 0px 10px 0px;
}

/* list items */
.entry-content ol:not(.wp-block-gallery) li,
.entry-content ul:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links) li {
	margin: 10px 0px;
	list-style: none;
}

.editor-styles-wrapper ul.wp-block[data-type="core/list"] li:before,
.editor-styles-wrapper ul.wp-block-latest-posts li:before,
.entry-content ul:not(.blocks-gallery-grid):not(.gform_fields):not(.wp-block-social-links) li:before,
.wc-block-product-categories-list li:before {
	font-size: 14px;
	content: "\f18e";
	font-family: fontawesome;
	position: relative;
	margin-right: 16px;
