	margin: 5px;
	border-radius: 34px;
	font-size: 12px;
	text-transform: uppercase;
	font-weight: bold;
	font-family: $headings-font-family;
	padding: 12px 33px;
	text-shadow: none;
	box-shadow: none;
	border: none;
	float: none;
	display: inline-block;
	max-width: 48%;
}

.byconsolewooodt_delivery_type input:checked+label[for="byconsolewooodt_delivery_type_levering"] {
	background-color: $primary;
}

/* woocommerce date + time picker */
.ui-datepicker {
	width: 275px;
	padding: 6px;
	max-width: 100%;
}

.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
	border-radius: 0px;
}

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default {
	padding: 4px;
}

.ui-widget-header {
	background-image: none;
	background: $gray-500;
	color: $dark;
}

.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
	top: 50%;
	transform: translateY(-50%);
}

.ui-timepicker-wrapper,
.ui-datepicker-calendar,
.ui-datepicker .ui-datepicker-title {
	font-family: $font-family-base;
	font-size: 1.6em !important;
}

.ui-datepicker .ui-datepicker-title {
	text-transform: uppercase;
}

.ui-timepicker-list li {
	padding: 12px;
}

li.ui-timepicker-selected,
.ui-timepicker-list li:hover,
.ui-timepicker-list .ui-timepicker-selected:hover {
	background-color: $primary;
}

@media only screen and (max-width: 414px) {
	.woocommerce form .byconsolewooodt_delivery_type>.byconsolewooodt_ordertype_label {
		width: 100%;
		max-width: none;
		float: none;
		padding: 0px;
		margin: 0px;
	}
}

@media only screen and (min-width: 1px) {

	.woocommerce #byconsolewooodt_checkout_field .form-row.byconsolewooodt_delivery_date,
	.woocommerce #byconsolewooodt_checkout_field .form-row.byconsolewooodt_delivery_time {
		width: 48%;
		float: left;
	}

	.woocommerce #byconsolewooodt_checkout_field .form-row.byconsolewooodt_delivery_time {
		float: right;
	}

	.woocommerce #byconsolewooodt_checkout_field:after {
		content: '';
		height: 0;
		visibility: hidden;
	}

	.byc_service_time_closed,
	.woocommerce #byconsolewooodt_checkout_field:after {
		clear: both;
	}
}

